  &-md-100 {
    @include pad {
      width: 100% !important; } }
  &-md-auto {
    @include pad {
      width: auto !important; } }

  &-lg-100 {
    @include pc {
      width: 100% !important; } }
  &-lg-auto {
    @include pc {
      width: auto !important; } } }


.h {
  &-5 {
    height: 53px; }
  &-1xl {
    height: 120px; }
  &-2xl {
    height: 256px; }
  &-3xl {
    height: 345px; }
  &-4xl {
    height: 400px; }
  &-5xl {
    height: 559px; }

  &-md-1xl {
    @include pad {
      height: 126px; } }
  &-md-100 {
    @include pad {
      height: 100% !important; } }
  &-md-auto {
    @include pad {
      height: auto !important; } }

  &-lg-100 {
    @include pc {
      height: 100% !important; } }
  &-lg-auto {
    @include pc {
      height: auto !important; } } }


.min-height {
  &-3 {
    min-height: 345px; }

  &-md-3 {
    @include pad {
      min-height: 350px; } } }


.me-lg-n8 {
  @include pc {
    margin-right: -16px !important; } }


.border-lg-end {
  @include pc {
    border-right: 1px solid !important; } }


.border__bottom__secondary {
  position: relative;

  &::before {
    content: '';
    position: absolute;
    width: 0%;
    height: 1px;
    background: red;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    transition-duration: .2s; }

  &:hover {
    &::before {
      width: 100%; } }

  &--checked {
    &::before {
      width: 100%; } } }


.img-position-center {
  position: relative;
  overflow: hidden;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    object-fit: cover; } }


.link__dark--hover {
  color: $dark;
  &:hover {
    color: $secondary; } }


.text-shadow-sm {
  text-shadow: 0 0 5px rgba($dark, .5); }


.white-space-nowrap {
  white-space: nowrap; }


.overflow {
  &-y-scroll {
    overflow-y: scroll; }
  &-y-hidden {
    overflow-y: hidden; }
  &-y-auto {
    overflow-y: auto; }

  &-x-hidden {
    overflow-x: hidden; }
  &-x-auto {
    overflow-x: auto; } }


.transition-delay-3 {
  transition-delay: .3s; }


.z-index-999 {
  z-index: 999; }


.check-style-none {
  &:checked {
    box-shadow: none; }
  &:focus {
    box-shadow: none; } }


.scrollbar {
  // 
  &::-webkit-scrollbar {
    width: 2px;
    border-radius: 4px; }

  // 
  &::-webkit-scrollbar-thumb {
    border: solid 1px $secondary; }

  // 
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 3px 9px $light; } }
