@mixin mobile() { // sm
  @media ( min-width: 576px ) {
    @content; } }


@mixin pad() { // md
  @media ( min-width: 768px ) {
    @content; } }

// @mixin mobile()
// @media ( max-width: 767px )

@mixin pc() { // lg 
  @media ( min-width: 992px ) {
    @content; } }
